import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class BlogService {

  baseURL = "/assets";
  httpHeaders = new HttpHeaders({'Content-type':'application/json'});

  constructor(private http: HttpClient) { }

  getAllFormations(): Observable<any>{
    return this.http.get(this.baseURL + '/formations.JSON',
    {headers : this.httpHeaders});
  }
  getAllProjets(): Observable<any>{
    return this.http.get(this.baseURL + '/projets.JSON',
    {headers : this.httpHeaders});
  }

  getAllCompetences(): Observable<any>{
    return this.http.get(this.baseURL + '/competences.JSON',
    {headers : this.httpHeaders});
  }
}
